"use client";

import { useEffect, useState } from "react";

export default function DocumentList({ refreshKey }: { refreshKey: number }) {
  const [docs, setDocs] = useState([]);

  async function loadDocs() {
    const res = await fetch("http://localhost:3001/documents");
    const data = await res.json();
    setDocs(data);
  }

  useEffect(() => {
    loadDocs();
  }, [refreshKey]); // reload when upload completes

  return (
    <div className="space-y-4">
      {docs.map((doc: any) => (
        <div key={doc.id} className="p-4 border rounded bg-white shadow">
          <p className="font-medium">{doc.originalName}</p>
          <p className="text-sm text-gray-500">{doc.status}</p>

          <a
            href={`http://localhost:3001/documents/${doc.id}/download`}
            className="text-blue-600 underline text-sm mt-2 inline-block"
          >
            Download
          </a>
        </div>
      ))}
    </div>
  );
}
