"use client";

import { useState } from "react";

export default function UploadButton({ onUploadComplete }: { onUploadComplete: () => void }) {
  const [progress, setProgress] = useState(0);
  const [status, setStatus] = useState<"idle" | "uploading" | "done" | "error">("idle");
  const [errorMsg, setErrorMsg] = useState("");

  async function handleUpload(e: React.ChangeEvent<HTMLInputElement>) {
    if (!e.target.files?.length) return;

    const file = e.target.files[0];
    setStatus("uploading");
    setProgress(0);
    setErrorMsg("");

    const form = new FormData();
    form.append("file", file);

    const xhr = new XMLHttpRequest();

    xhr.upload.onprogress = (event) => {
      if (event.lengthComputable) {
        setProgress(Math.round((event.loaded / event.total) * 100));
      }
    };

    xhr.onload = () => {
      if (xhr.status === 200) {
        setStatus("done");
        setProgress(100);
        onUploadComplete(); // ðŸ”¥ refresh list
      } else {
        setStatus("error");
        setErrorMsg("Upload failed");
      }
    };

    xhr.onerror = () => {
      setStatus("error");
      setErrorMsg("Network error");
    };

    xhr.open("POST", "http://localhost:3001/upload");
    xhr.send(form);
  }

  return (
    <div className="space-y-3">
      <label className="cursor-pointer inline-block px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700">
        Upload Document
        <input type="file" className="hidden" onChange={handleUpload} />
      </label>

      {status === "uploading" && (
        <div className="w-full bg-gray-200 h-3 rounded overflow-hidden">
          <div
            className="bg-blue-600 h-full transition-all"
            style={{ width: `${progress}%` }}
          />
        </div>
      )}

      {status === "done" && <p className="text-green-600">Upload complete!</p>}
      {status === "error" && <p className="text-red-600">{errorMsg}</p>}
    </div>
  );
}
